/* filtro nas telas grandes ok */
@media screen and (min-width: 1199px) {


  .filter__ret--purple {
    position: fixed;

    width: auto;
    height: auto;

  /* alinha o icone de novo na borda */
    justify-content: space-between;
  
    
    
  }

  .filter__form{
    margin: 1em 0;
    padding-right: 2em;
    padding-bottom: 1em;
    opacity: 1;
    
    
  }

  .filter__form input{
    margin-right: 0.62em;
    
  }

  form .h3 {
    padding: 0.3em 0 0 0;
    font-weight: bold;
  }
  
  svg {
    /*escondendo o ícone*/
  opacity: 0;
  
  
  }
}

