/* BARRA DE BUSCA ------------------ */
.search__text {
  text-align: center;
}

.search__searchBar{

  max-width: 80%;
  height: 2.5em;

 
  font-size: 16px;
  font-weight: thin;
  /* colocar o texto mais pro lado */
  padding-left: 1em;
  background: #FFFFFF;
  border: 2px solid var(--light-purple);
  box-sizing: border-box;
  border-radius: 1.5em;
  opacity: 1;
  align-items: center
  
}

.search__searchBar:hover {
  opacity: 0.7;
  cursor: pointer;
  transition: all .3s ease-in-out;
  
  

}

.search__button {
  width: 7.5em;
  height:  2.5em;
  background: var(--light-purple);
  border: 2px solid var(--purple);
  color: var(--purple);
  box-sizing: border-box;
  border-radius: 6px;
  font-weight: bold;
  /* fazer o botão "pesquisar"mudar de cor quando passa o mouse */
  opacity: 1;
}

.search__button:hover {
  opacity: 0.9;
  cursor: pointer;
  transition: all .3s ease-in-out;
}



@media screen and (min-width: 1024px) {
  .search__searchBar{
    max-width: 32.5em;
  
  }

  .search {
    padding-left: calc((100% - 940px)/2);
    padding-right: calc((100% - 940px)/2);

    
  }
    
}


